import React from 'react';
import Link from 'gatsby-link';

import { TitleBlock } from './TitleBlock';
import { SkillsList } from './SkillsList';
import { SecurityBlock } from './Security';
import { ExtraBlock } from '../../components/ExtraBlock';
import './index.scss';

export default ({ data }) => {
    const page = data.markdownRemark;
    const { title, titleBlock, skillsets, extraBlock, security } = page.frontmatter;

    return (
        <div className="skills__container">
            <TitleBlock params={titleBlock} />

            <SkillsList params={skillsets} />

            <SecurityBlock params={security} />

            <ExtraBlock params={extraBlock} bg={'backgrounds/skills.png'} />
        </div>
    );
};

export const query = graphql`
    query SkillsPageQuery {
        markdownRemark(fields: { slug: { eq: "/skills/" } }) {
            html
            frontmatter {
                title
                titleBlock {
                    title
                    description
                }
                skillsets {
                    isColumn
                    image
                    reversed
                    skills {
                        title
                        icon
                        description
                        color
                    }
                }
                extraBlock {
                    title
                    description
                    inputPlaceholder
                    buttonText
                }
                security {
                    title
                    description
                    text
                }
            }
        }
    }
`;



