import React from 'react';
import { string, shape } from 'react-proptypes';

import './TitleBlock.scss';

const TitleBlock = ({ params }) => (
    <div className="title-block__container">
        <img src={`${process.env.ASSETS_URL}backgrounds/skills-title.png`} />

        <div className="title-block__content">
            <h1>
                {params.title}
            </h1>

            {
                params.description.split('<br/>').map((item, index) => (
                    <p  className="copytext"
                        key={index}
                        dangerouslySetInnerHTML={{ __html: item }} />
                ))
            }

        </div>
    </div>
);

TitleBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       inputPlaceholder: string,
       buttonText: string
    })
};

export default TitleBlock;



